@import url('https://fonts.googleapis.com/css?family=Lato&display=swap');

* {
  font-family: 'Lato', sans-serif;
}

body{
  text-align: center;
  width: 70%;
  margin: auto;
}

h1{
  font-size: 7vw;
}

h2{
  font-size: 3vw;
  margin-bottom: 50px;
}

.bio{
  text-align: justify;
  margin: auto;
  width: 50%;
  font-size: 30px;
}

p{
  font-size: 20px;
  margin-bottom: 50px;
}
.maindiv{
  position: relative;
  z-index: 2;
  width: 80%;
  margin: auto;
}

.buttonbox{
  box-sizing: border-box;
  display: flex;
  flex-direction: row;
  margin: auto;
  justify-content: space-evenly;
  width: 80%;
  height: 210px;
  border: 5px solid black;
  color: black;
  /* box-shadow: 0px 0px 50px #888888; */
}

.buttonw, .buttonb, .buttonc {
  color: black;
  width: 33.33%;
  height: 100%;
  text-decoration: none;
  font-weight: 1000;
  font-size: 2vw;
  font-family: 'lato', sans-serif;
  transition: ease-out .3s;
  line-height: 200px;
}

.buttonw:hover, .buttonc:hover, .buttonb:hover{
  background-color: #000000;
  cursor: grab;
  color: #ffffff;
  font-size: 2.9vw;
}

.workbox, .workbox2{
  margin: auto;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  flex-wrap: wrap;
  width: 80%;
  height: 1000;
}

.worknav{
  box-sizing: border-box;
  border: 5px solid black;
  margin: auto;
  width: 50%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  /* padding: 50px; */
}

.link{
  text-decoration: none;
  font-weight: 1000;
  color: black;
  font-size: 2vw;
  transition-duration: 0.2s;
  width: 100%;
  height: 100%;
  line-height: 150px;
}

.link:hover{
  color: white;
  background-color: black;
  height: 100px;

}

.ballcontainer{
  position: absolute;
  left: 0px;
  top: 0px;
  z-index: 1;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  width: 100%;
  height: 100%;
}
.ball{
  /* margin: 50px; */
  width: 50px;
  height: 50px;
  /* border-radius: 100px; */
  background-color: white;
  transition-duration: 0.2s;
}
.ball:hover{
  background-color: black;
}

.sound-container{
  display: flex;
  flex-direction: row;
  width: 80%;
  margin: auto;
}

.video-container{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  width: 100%;
  margin: auto;
}
